import { Button, Badge, Text, Flex } from "@chakra-ui/react";
import { useCallback, useEffect, useState } from "react";
import { useWeb3ExecuteFunction } from "react-moralis";
import { BSC_COINS_SUPPORTED } from "../COINS_SUPPORTED";
import { BIGNFT_CONTRACT_ADDRESS, ABI_BIGNFT } from "../CONTRACT_DETAILS";
import { Moralis } from "moralis-v1";

function BuyButton({
  coin,
  bigNFTAmount,
  toast,
  showErrToast,
  showInfoToast,
  setReRender,
  reRender,
}) {
  /**
   * Moralis : START
   */
  const { fetch: bigFetch, isFetching: bigIsFetching } =
    useWeb3ExecuteFunction();

  /**
   * Moralis : END
   */

  const [BNNValueForBIGNFT, setBNNValueForBIGNFT] = useState(0);

  const buyBigNFT = useCallback(() => {
    // console.log(`Bought ${bigNFTAmount} BIGNFTs, using - ${coin}`);
    let buyingCoin = coin + "";
    buyingCoin = buyingCoin.toUpperCase();
    console.log(`trying to buy ${bigNFTAmount} $BIGNFT w/ ${coin}`);
    if (!bigNFTAmount || Number(bigNFTAmount) < 10) {
      showInfoToast("Minimum buying amount is 10 BIGNFT");
      console.error(`dev-error Minimum buying amount is 10BIGNFT`);
      return;
    }

    let fnName = "buyWithIBAT";
    let buyParams = {
      amount: bigNFTAmount,
    };
    let options = {
      abi: ABI_BIGNFT,
      contractAddress: BIGNFT_CONTRACT_ADDRESS,
      functionName: fnName,
      params: buyParams,
    };
    // const swapOptions = {
    //   msgValue: Moralis.Units.ETH(`${BNNValueForBIGNFT}`),
    // };

    // if (buyingCoin === "IBAT") {
    //   console.log(`Bought ${bigNFTAmount} BIGNFTs, w/ IBAT`);
    // } else
    if (buyingCoin === "BNB") {
      console.log(`Bought ${bigNFTAmount} BIGNFTs, w/ BNB`);
      options = {
        ...options,
        msgValue: Moralis.Units.ETH(`${BNNValueForBIGNFT}`),
      };
    } else if (
      buyingCoin === "USDT" ||
      buyingCoin === "BUSD" ||
      buyingCoin === "USDC"
    ) {
      console.log(`Bought ${bigNFTAmount} BIGNFTs, w/ StableCoin`);
      let stableCoinIndex = 0; // for USDT
      if (buyingCoin === "USDC") {
        stableCoinIndex = 1;
      } else if (buyingCoin === "BUSD") {
        stableCoinIndex = 2;
      } else if (buyingCoin === "DAI") {
        stableCoinIndex = 3;
      }
      buyParams = {
        ...buyParams,
        purchaseToken: stableCoinIndex,
      };
    }

    bigFetch({
      params: options,
      onSuccess: (tx) => {
        console.log(tx);
        toast({
          containerStyle: {
            maxWidth: "92vw",
          },
          title: `Buying ${bigNFTAmount} $BIGNFT tokens Tx in progress...`,
          description: `Tx Hash : ${tx.hash}`,
          status: "info",
          duration: null,
          isClosable: true,
        });

        tx.wait().then((dataa) => {
          console.log(dataa);

          setReRender(!reRender); // for refreshing values
          // fetchERC20Balances();

          toast.closeAll();
          toast({
            containerStyle: {
              maxWidth: "92vw",
            },
            title: `Success. Bought ${bigNFTAmount} $BIGNFT tokens`,
            description: `Tx Hash : ${dataa.transactionHash}`,
            status: "success",
            duration: "10000",
            isClosable: true,
          });
        });
      },
      onError: (err) => {
        console.error(err);
        if (err && err.data) showErrToast(err.data.message);
        else if (err && err.message) showErrToast(err.message);
        console.error(`dev-failed buying BIGNFT`);
      },
    });
  }, [
    coin,
    bigNFTAmount,
    bigFetch,
    BNNValueForBIGNFT,
    toast,
    reRender,
    showErrToast,
    showInfoToast,
    setReRender,
  ]);

  const enableAllowance = useCallback(() => {
    let tokenToEnable = `${coin.toUpperCase()}`;
    console.log(`enableAllowance-`);
    console.log(tokenToEnable);

    let ABI_approve = BSC_COINS_SUPPORTED.filter(
      (e) => e.symbol.toUpperCase() === tokenToEnable
    )[0].ABI_approve;

    // console.log(ABI_approve);
    let tokenToEnableAllowanceContractAddress = BSC_COINS_SUPPORTED.filter(
      (e) => e.symbol.toUpperCase() === tokenToEnable
    )[0].bscContractAddress;

    let contractAddressToAllow = BIGNFT_CONTRACT_ADDRESS;
    let optionsApprove = {
      abi: ABI_approve,
      contractAddress: tokenToEnableAllowanceContractAddress,
      functionName: "approve",
      params: {
        spender: contractAddressToAllow,
        amount: "100000000000000000000000000000",
      },
    };

    bigFetch({
      params: optionsApprove,
      onSuccess: (tx) => {
        console.log(tx);
        toast({
          containerStyle: {
            maxWidth: "92vw",
          },
          title: `Allowance for ${tokenToEnable} Tx in progress...`,
          description: `Tx Hash : ${tx.hash}`,
          status: "info",
          duration: null,
          isClosable: true,
        });

        tx.wait().then((dataa) => {
          console.log(dataa);
          console.log(`dev-✅-${tokenToEnable} allowance approved`);
          window.localStorage.setItem(`${tokenToEnable}allowance`, "true");

          setReRender(!reRender); // for refreshing values

          toast.closeAll();
          toast({
            containerStyle: {
              maxWidth: "92vw",
            },
            title: `${tokenToEnable} allowance enabled. For best Experience, disconnect & connect again.`,
            description: `Tx Hash : ${dataa.transactionHash}`,
            status: "success",
            duration: "10000",
            isClosable: true,
          });
        });
      },
      onError: (err) => {
        console.error(err);
        if (err && err.data) showErrToast(err.data.message);
        else if (err && err.message) showErrToast(err.message);
        console.error(`dev-failed ${tokenToEnable} allownace`);
      },
    });
  }, [bigFetch, coin, reRender, setReRender, showErrToast, toast]);

  /**
   * START
   * Gets the amount of max sold coins required to buy given amount of BIGNFT
   */
  const [maxSold, setMaxSold] = useState("0");
  const getMaxSold = useCallback(() => {
    let c = coin + "";
    c = c.toUpperCase();

    if (!bigNFTAmount || Number(bigNFTAmount) < 10 || c.length <= 0) {
      setMaxSold("0");
      return;
    }

    let fnName = "getIBATAmount";
    let buyingPrice = 0;
    let options = {
      abi: ABI_BIGNFT,
      contractAddress: BIGNFT_CONTRACT_ADDRESS,
      functionName: fnName,
      params: {
        amount: bigNFTAmount,
      },
    };
    bigFetch({
      params: options,
      onSuccess: (tx) => {
        buyingPrice = Number(tx._hex) + "";
        buyingPrice = Moralis.Units.FromWei(
          buyingPrice,
          BSC_COINS_SUPPORTED.filter((e) => e.symbol === c)[0].decimal
        );
        buyingPrice = Number(buyingPrice).toFixed(3) + "";
        console.log(
          `buyingPrice - ${buyingPrice} ${coin} - for ${bigNFTAmount} $BIGNFT`
        );
        setMaxSold(buyingPrice);
      },
      onError: (err) => {
        console.error(err);
        console.error(`dev-failed get price - ${fnName}`);
      },
    });
    // if(c.toUpperCase() === "IBAT"){

    // }
  }, [coin, bigNFTAmount, bigFetch]);

  // calls the setMaxSold everytime the amount of BIGNFT to buy changes
  useEffect(() => {
    getMaxSold();
  }, [getMaxSold]);
  /**
   * Gets the amount of max sold coins required to buy given amount of BIGNFT
   * END
   */

  return (
    <>
      <Button
        onClick={buyBigNFT}
        disabled={
          window.localStorage.getItem(
            `BIGNFT_${coin.toUpperCase()}allowance`
          ) === "false" || bigIsFetching
        }
      >
        {bigIsFetching ? (
          "Sign Transaction in wallet"
        ) : (
          <>
            BUY BIGNFT
            {coin.toUpperCase() === "IBAT" && (
              <Badge ml={".5rem"} colorScheme={"green"}>
                10% Discount
              </Badge>
            )}
          </>
        )}
      </Button>
      {window.localStorage.getItem(`BIGNFT_${coin.toUpperCase()}allowance`) ===
        "false" && <Button onClick={enableAllowance}>ENABLE {coin}</Button>}
      <Flex justifyContent={"space-between"}>
        <Text fontSize={"x-small"}>Max Sold</Text>
        <Text fontSize={"x-small"}>
          {maxSold} {coin}
        </Text>
      </Flex>
    </>
  );
}

export default BuyButton;
