import React, { useEffect, useCallback, useState } from "react";
import "./styles/App.scss";
import { useMoralis, useWeb3ExecuteFunction } from "react-moralis";
import { ABI_BIGNFT, BIGNFT_CONTRACT_ADDRESS, TOTAL_TOKENS_FOR_PRESALE } from "./CONTRACT_DETAILS";

import Header from "./components/Header";
import Main from "./components/Main";
import bg from "./assets/bg.png"
import { Flex, useMediaQuery } from "@chakra-ui/react";
import { BSC_COINS_SUPPORTED } from "./COINS_SUPPORTED";

// FIREBASE - START
import { initializeApp } from "firebase/app";
import { getDatabase, ref, update } from "firebase/database"; //to update value of tokensSold
// import { getAnalytics } from "firebase/analytics";
// FIREBASE - END

// ---------- APP -------------
function App() {
  const [reRender, setReRender] = useState(false);

  const {
    authenticate,
    isWeb3Enabled,
    isAuthenticated,
    user,
    enableWeb3,
    isWeb3EnableLoading,
    Moralis,
  } = useMoralis();

  const [isLargerThan500] = useMediaQuery("(min-width: 500px)");

  useEffect(() => {
    if (!isWeb3Enabled && isAuthenticated) {
      // console.log(`useEffect - App.js`);
      // console.log(`isWeb3Enabled - ${isWeb3Enabled}`);
      // console.log(`isAuthenticated - ${isAuthenticated}`);
      // console.log(`isWeb3EnableLoading - ${isWeb3EnableLoading}`);
      if (isWeb3EnableLoading)
        Moralis.enableWeb3()
          .then(() => {
            console.log(`isWeb3Enabled✅ - ${Moralis.isWeb3Enabled()}`);
            console.log("web3 activated");
          })
          .catch(() => {
            console.log("❌web3 activation failed");
            console.log(`isWeb3Enabled❌- ${Moralis.isWeb3Enabled()}`);
          });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isWeb3Enabled, isAuthenticated, enableWeb3]);


  /**
   * Moralis - START
   */
  const { fetch } = useWeb3ExecuteFunction();
  /**
   * Moralis - END
   */

  /**
   * FIREBASE- START
   * 
   */
  const firebaseConfig = {
    apiKey: "AIzaSyARJJ1R6Jfb0ffNEJHIevrwlScwW40qqsc",
    authDomain: "battleinfinityio.firebaseapp.com",
    databaseURL: "https://battleinfinityio-default-rtdb.firebaseio.com",
    projectId: "battleinfinityio",
    storageBucket: "battleinfinityio.appspot.com",
    messagingSenderId: "694775551331",
    appId: "1:694775551331:web:a63eda8773e72f371daf56",
    measurementId: "G-7Q5BZRQWKL"
  };
  // Initialize Firebase
  const firebaseApp = initializeApp(firebaseConfig);
  const db = getDatabase();
  // const analytics = getAnalytics(firebaseApp);



  /**
   * checking allowance - START
   */
  const hasAllowance = useCallback(async (userAddress, spenderContractAddress, tokenAddressToCheck) => {
    let ABI_allowance = BSC_COINS_SUPPORTED.filter(e => e.bscContractAddress.toUpperCase() === tokenAddressToCheck.toUpperCase())[0].ABI_allowance;
    tokenAddressToCheck = tokenAddressToCheck && tokenAddressToCheck.length > 0 ? tokenAddressToCheck : "0x19cd9b8e42d4ef62c3ea124110d5cfd283ceac43";
    // tokenAddressToCheck = "0x19cd9b8e42d4ef62c3ea124110d5cfd283ceac43"; // token address of IBAT

    let optionsAllowance = {
      abi: ABI_allowance,
      contractAddress: tokenAddressToCheck,
      functionName: "allowance",
      params: {
        owner: userAddress,
        spender: spenderContractAddress,
      },
    };
    // console.log(`optionsAllowance - ${tokenAddressToCheck}`);
    // console.log(optionsAllowance);
    let hasAl = false;
    await fetch({
      params: optionsAllowance,
      onSuccess: (data) => {
        if (Number(data._hex) !== 0) {
          console.log(`✅allowance - ${tokenAddressToCheck}`);
          hasAl = true;
        } else {
          console.log(`❌allowance - ${tokenAddressToCheck}`);
        }
      },
      onError: (err) => {
        console.error(err);
        console.error(`dev-❌failed to run allowance function`);
      },
    });
    return hasAl;
  }, [fetch])


  useEffect(() => {
    if (!isWeb3Enabled || !isAuthenticated) return;
    let panCONTRACT_ADDRESS = "0xe4d629d2DF66714eD0dd4EF8e27B3c69746d72e3";
    panCONTRACT_ADDRESS = BIGNFT_CONTRACT_ADDRESS;
    BSC_COINS_SUPPORTED.forEach((token) => {
      if (token.symbol.toUpperCase() !== "BNB") {
        // @reminder - not sure if we need to check allowance in this one - if so - the spender will be BIGNFT contract addres right? asked Jagjeet this - waiting for response to do further development
        console.log(`reRender(${reRender}) Value changed - so refereshing`);
        hasAllowance(user.get("ethAddress"), panCONTRACT_ADDRESS, token.bscContractAddress).then((hasAl) => {
          console.log(`${token.symbol} - swap allowance - ${hasAl}`);
          if (hasAl) window.localStorage.setItem(`BIGNFT_${token.symbol.toUpperCase()}allowance`, "true");
          else window.localStorage.setItem(`BIGNFT_${token.symbol.toUpperCase()}allowance`, "false");
        });
      }
    })
    window.localStorage.setItem(`BIGNFT_BNBallowance`, "true");
    // tokenAllowance removed when logging out - window.localStorage.removeItem
  }, [isWeb3Enabled, isAuthenticated, user, hasAllowance, reRender]);
  /**
   * checking allowance - END
   */

  /**
   * START
   * Checking how many tokens are left for sale
   */
  const updateTokensSoldinDB = useCallback((tokensSold) => {
    console.log(`🪙$BIGNFT tokensSold  - ${tokensSold}`);
    if (!tokensSold || tokensSold === 0) return;
    update(ref(db), { "tokensSold": tokensSold }).then(() => {
      console.log(`🟢updateTokensSoldinDB - tokensSold - ${tokensSold}`);
    }).catch(console.log(`dev-failed-🔴updateTokensSoldinDB`));
  }, [db]);

  const getTotalBIGNFTSold = useCallback(() => {
    console.log(`reRender[${reRender}] changed, so updating values - (getTotalBIGNFTSold).`);
    const totalTokens = TOTAL_TOKENS_FOR_PRESALE;
    const fnName = "inSale";
    let options = {
      abi: ABI_BIGNFT,
      contractAddress: BIGNFT_CONTRACT_ADDRESS,
      functionName: fnName,
    };
    fetch({
      params: options,
      onSuccess: (data) => {
        const tokenLeftToSold = Number(data._hex);
        const tokensSold = totalTokens - tokenLeftToSold;
        console.log(tokensSold);
        updateTokensSoldinDB(tokensSold)
      },
      onError: (err) => {
        console.error(err);
        console.error(`dev-❌failed to getTotalBIGNFTSold`);
      },
    });
  }, [fetch, updateTokensSoldinDB, reRender]);

  useEffect(() => {
    if (!isWeb3Enabled) return;
    getTotalBIGNFTSold();
  }, [isWeb3Enabled, getTotalBIGNFTSold])
  /**
   * END
   * Checking how many tokens are left for sale
   */

  const login = async (provider) => {
    if (!isAuthenticated) {
      let signingMessage = "$BIGNFT Authentication - Battle Infinity";
      if (!provider) {
        await authenticate({ signingMessage: signingMessage })
          .then(async (user) => {
            console.log("logged in user:", user);
            user && console.log(user.get("ethAddress"));
            user &&
              window.localStorage.setItem(
                "BIGNFT_userAddress",
                user.get("ethAddress")
              );
          })

          .catch(function (error) {
            console.log(error);
            console.error(`dev-failed to authenticate`);
          });
      } else if (provider === "walletconnect") {
        console.log(`WC - ${provider === "walletconnect"}`);
        console.log(`Logging with ${provider} - app`);
        await authenticate({
          provider: "walletconnect",
          chainId: 56,
          signingMessage: signingMessage,
        }).then((user) => {
          console.log(`isAuthenticated - ${isAuthenticated}.`);
          console.log("logged in user:", user);
          console.log(user.get("ethAddress"));
          console.log(`isAuthenticated - ${isAuthenticated}.`);
          user && console.log(user.get("ethAddress"));
          user &&
            window.localStorage.setItem(
              "BIGNFT_userAddress",
              user.get("ethAddress")
            );
        });
        try {
          console.log("logged in user:", user);
          user && console.log(user.get("ethAddress"));
        } catch (error) {
          console.log(error);
          console.error(`dev-failed to authenticate with walletconnect`);
        }
      }
    }
  };


  return (
    <Flex className="app" direction="column" backgroundImage={bg} backgroundPosition={isLargerThan500 ? "center" : "right"} backgroundSize={"cover"} >

      <Header login={login}></Header>
      <Main
        login={login}
        reRender={reRender}
        setReRender={setReRender}
        firebaseApp={firebaseApp}
      ></Main>
    </Flex>
  );
}

export default App;
